.header{
    background-color: black;
    height: 60px;
}

.reocine{
    margin: 0;
}

.reocine a{
    text-decoration: none;
    color: white;
    font-size: 40px;
    margin-left: 10px;
}

.wrapper{
    display: flex;
    background-color: white;
}

.section{
    text-align: left;
    padding: 5%;
    height: 100vh;
}

.title{
    margin-bottom: 30px;
}

nav>ul{
    display: inline-block;
    padding: 0;
}

li{
    list-style: none;
    float: left;
    width: 100px;
    margin-right: 20px;
    font-weight: 600;
}

li:first-child{
    width: 100px;
    font-weight: 600;
}

.description{
    width: 800px;
}

.aside{
    padding: 5%;
}

.coverImg{
    width: 350px;
    float: right;
    margin-right: 10%;
}