.container {
  height: 100%;
  display: flex;
  justify-content: center;
}
  
.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader p{
  font-size: 100px;
  font-weight: 700;
}

.header{
  background-color: black;
  height: 60px;
}

.reocine{
  margin: 0;
}

.reocine a{
  text-decoration: none;
  color: white;
  font-size: 40px;
  margin-left: 10px;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  width: 100%;
  padding-top: 70px;
}
  
@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
  }
}